.input-range__slider {
	appearance: none;
	background: #3f51b5;
	border: 1px solid #3f51b5;
	border-radius: 100%;
	cursor: pointer;
	display: block;
	height: 1rem;
	margin-left: -0.5rem;
	margin-top: -0.65rem;
	outline: none;
	position: absolute;
	top: 50%;
	transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
	width: 1rem;
}

.input-range__slider:active {
	transform: scale(1.3);
}

.input-range__slider:focus {
	box-shadow: 0 0 0 5px rgba(63, 81, 181, 0.2);
}

.input-range--disabled .input-range__slider {
	background: #ccc;
	border: 1px solid #ccc;
	box-shadow: none;
	transform: none;
}

.input-range__slider-container {
	transition: left 0.3s ease-out;
}

.input-range__label {
	color: #2d2d2d;
	font-family: "Arial";
	font-weight: bold;
	font-size: 0.6rem;
	transform: translateZ(0);
	white-space: nowrap;
}

.input-range__label--min,
.input-range__label--max {
	display: none;
}

.input-range__label--min {
	left: 0;
}

.input-range__label--max {
	right: 0;
}

.input-range__label--value {
	position: absolute;
	top: -1.4rem;
}

.input-range__label-container {
	left: -50%;
	position: relative;
}

.input-range__label--max .input-range__label-container {
	left: 50%;
}

.input-range__track {
	background: #eee;
	border-radius: 0.3rem;
	cursor: pointer;
	display: block;
	height: 0.3rem;
	position: relative;
	transition: left 0.3s ease-out, width 0.3s ease-out;
}

.input-range--disabled .input-range__track {
	background: #eee;
}

.input-range__track--background {
	left: 0;
	margin-top: -0.15rem;
	position: absolute;
	right: 0;
	top: 50%;
}

.input-range__track--active {
	background: #3f51b5;
}

.input-range {
	height: 1rem;
	position: relative;
	width: 100%;
}
